




















































import mTitle from '@/components/m-title'
import mVipCard from '@/components/m-vip-card'
export default {
	components: {
		mTitle,
		mVipCard
	},
	data() {
		return {
			chooseVipId:0,
			isVip:false,//是否为会员
			myMember:{
				nickName:'',
				dueTime:'',
				memberType:'',
				discount:'',
				memberSynopsis:''
			},
			members:[],
			userData:{},
			vipName:"",
			vipDescribes:"" //会员卡权益
		};
	},
	methods:{
		getUser(){
			this.userData = JSON.parse(uni.getStorageSync('userData'));
			if(!this.userData.avatarUrl){
				this.$set(this.userData,'avatarUrl',this.userData.headAddress||'')
			}
			if(!this.userData.nickName){
				this.$set(this.userData,'nickName',this.userData.nickname||'')
			}
		},
		// 会员列表
		getVips(){
			let _this = this;
			this.$apis.postMembers({}).then(res=>{
					_this.members=res.data.members;
					if(!_this.isVip){
						//非会员
						this.chooseVipId=_this.members[0].id;
						this.vipName=_this.members[0].synopsis;
						this.vipDescribes=_this.members[0].describes;
					}
			})
		},
		// 选择会员卡
		chooseVip(res){
			// console.log(res);
			this.chooseVipId=res.id,
			this.vipName=res.synopsis;
			this.vipDescribes=res.describes;
		},
		changeType(memberType){
			switch(memberType){
				case '0':
				return "月";
				case '1':
				return "季";
				case '2':
				return "半年";
				case '3':
				return "年";
			}
		},
		//我的会员
		myVips(){
			let _this = this;
			this.$apis.postMyMember({}).then(res=>{
				
					let data = res.data.myMember;
					if(data){
						// 会员
						_this.isVip = true
						if(data&&data['memberType']){
							data['memberType']= _this.changeType(data['memberType']);
						}
						if(data&&data['discount']){
							data['discount']=data['discount'];
						}
						_this.myMember=data;
						_this.chooseVipId=data['memberId'];
						_this.vipName=data['discount'];
						_this.vipDescribes=data['memberSynopsis'];
					}else{
						// 非会员
						_this.isVip = false
					}
					this.getVips();
				
			})
		},
		//购买vip
		buyVipFn(){
			let _this = this;
			this.$apis.postBuyMember(_this.chooseVipId).then(res=>{
				let data =res.data;
				if(data){
					let paydata = {
						provider: 'wxpay',
						timeStamp: data.timeStamp+'',
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign
					}
					uni.requestPayment({
						...paydata,
						success: function(res) {
							uni.showModal({
								title: '支付成功',
								content: '恭喜哦，你已成功成为会员用户',
								showCancel:false,
								confirmText:'完成',
								success: function (res) {
									if (res.confirm) {
										_this.initData()
										uni.switchTab({  
											url: '/pages/tabBar/user'  
										});
									} 
									else if (res.cancel) {
										_this.initData()
										// console.log('用户点击取消');
									}
								}
							});
						},
						fail: function(err) {
							console.log(err);
						}
					})
				}
				
			}).catch(err=>{
				console.log(err)
			})
		},
		initData(){
			this.getUser();
			
			this.myVips();
		}
	},
	onLoad(options){
		this.initData()
	}
}
